/*DROP DOWN*/


/*Dropdown part 2*/
/* $menu_WIDTH: 150px; */
 
.menu {
  display: block;
  position: relative;
  cursor: pointer;
}

.menu-title {
  display: block;
  /* width: $menu_WIDTH;
  height: 40px;
  padding: 12px 0 0;
  background: #9dc852; */
  text-align: center;
  color: #ffffff;
  /* font-weight: bold;
  text-transform: uppercase; */
  transition: 0.3s background-color;
}

.menu-title:before {
  content: "";
  display: block;
  height: 0;
  /* border-top: 5px solid #9dc852;
  border-left: ($menu_WIDTH / 2) solid transparent;
  border-right: ($menu_WIDTH / 2) solid transparent;
  border-bottom: 0 solid #dddddd; */
  position: absolute;
  top: 100%;
  left: 0;
  z-index: 101;
  transition:
    0.2s 0.2s border-top ease-out,
    0.3s border-top-color;
}

/* .menu-title:hover { background: #8db842; }
.menu-title:hover:before { border-top-color: #8db842; } */

.menu:hover > .menu-title:before {
  border-top-width: 0;
  transition:
    0.2s border-top-width ease-in,
    0.3s border-top-color;
}

.menu-title:after {
  content: "";
  display: block;
  /* height: 0;
  border-left: ($menu_WIDTH / 2) solid transparent;
  border-right: ($menu_WIDTH / 2) solid transparent;
  border-bottom: 0 solid #ebebeb; */
  position: absolute;
  bottom: 0;
  left: 0;
  z-index: 101;
  transition: 0.2s border-bottom ease-in;
}

.menu:hover > .menu-title:after {
  border-bottom-width: 0px;
  transition: 0.2s 0.2s border-bottom-width ease-out;
}
 
.menu-dropdown {
  min-width: 100%;
  padding: 0px 0;
  position: absolute;
  background: #000000;
  z-index: 100;
  transition:
    0.5s padding,
    0.5s background;
}

.menu-dropdown:after {
  content: "";
  display: block;
  height: 0;
  /* border-top: 5px solid #ebebeb;
  border-left: ($menu_WIDTH / 2) solid transparent;
  border-right: ($menu_WIDTH / 2) solid transparent; */
  position: absolute;
  top: 100%;
  left: 0;
  z-index: 101;
  transition: 0.5s border-top;
}

.menu:not(:hover) > .menu-dropdown {
  padding: 0px 0;
  background: #000000;
  z-index: 99;
}

.menu:not(:hover) > .menu-dropdown:after {
  border-top-color: #000000;
}

.menu:not(:hover) > .menu-title:after {
  border-bottom-color: #000000;
}

.menu-dropdown > * {
  overflow: hidden;
  height: 30px;
  padding: 5px 10px;
  background: rgba(0,0,0,0);
  white-space: nowrap;
  transition: 
    0.5s height cubic-bezier(.73,.32,.34,1.5),
    0.5s padding cubic-bezier(.73,.32,.34,1.5),
    0.5s margin cubic-bezier(.73,.32,.34,1.5),
    0.5s 0.2s color,
    0.2s background-color;
}

.menu-dropdown > *:hover {
  background: rgba(0,0,0,0.1);
}

.menu:not(:hover) > .menu-dropdown > * {
  visibility: hidden;
  height: 0;
  padding-top: 0;
  padding-bottom: 0;
  margin: 0;
  color: rgba(25,25,25,0);
  transition: 
    0.5s 0.1s height,
    0.5s 0.1s padding,
    0.5s 0.1s margin,
    0.3s color,
    0.6s visibility;
  z-index: 99;
}

/* .hList {
  //overflow: hidden;
}
 */
.hList > * {
  float: left;
}

.hList > * + * {
  margin-left: 0;
}

